@use '../abstracts/breakpoints';


.button {
    display: block;
    width: max-content;
    background-color: var(--color-accent);
    border-radius: 5px;
    padding: 1em 2em;
    text-transform: uppercase;
    letter-spacing: 4px;
    box-shadow: 5px 5px 30px var(--color-primary);
    transition: all .25s ease-in-out;

    &:hover,
    &:hover > i {
        color: var(--color-accent);
        background-color: var(--color-primary);
    }

    &[data-visual=big] {
        font-size: var(--step-0);
    }

    &[data-visual=regular] {
        font-size: var(--step--1);

    }

    &[data-visual=small] {
        font-size: var(--step--2);
    }
}

.top-header__button {
    display: none;
    width: 30px;
    aspect-ratio: 1;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 10;

    &[aria-pressed=false] {
        background-image: url('../img/hamburger-icon.svg');
    }

    &[aria-pressed=true] {
        background-image: url('../img/hamburger-close-icon.svg');
    }

    @media (max-width: map-get(breakpoints.$breakpoints, 'tablet')) {
        display: inline-block;
    }
}